import { theme } from '@ali/4ever-bamboo';
import { CardData } from '@ali/4ever-mo';
import { ILinkCardInfo, LinkCardMetaData } from '../../types';

const { link } = theme;


export default function buildLinkCardData(data: { text: string, href: string, cardInfo: ILinkCardInfo }): CardData<LinkCardMetaData> {
  const { href, cardInfo, text } = data;
  return {
    cardType: 'link', height: link.height, spacing: {
      before: 10,
      after: 10,
    },
    metadata: { text, href, cardInfo: { ...cardInfo } }
  }
}